/* Skills container (optional) */
.skills-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
    justify-content: center;
    margin-top: 1.5rem;
   
}
.skill-title{
    text-align: center;
    color: #f97316;
    font-size: 2rem;
    font-weight: 600;
    margin-top: 2rem;
}

/* Skill icon wrapper */
.skill-icon {
  width: 60px;
  height: 60px;
  background: rgba(17, 24, 39, 0.95);
  border: 2px solid #111827;
  border-radius: 12px;

  display: flex;
  justify-content: center;
  align-items: center;

  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
}

/* Icon itself */
.skill-icon img,
.skill-icon svg {
  width: 32px;
  height: 32px;
  fill: #f97316;     /* for SVG logos */
  color: #f97316;    /* for react-icons */
  transition: 0.3s ease;
    width: 80px;  /* adjust size as needed */
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: rotate 3s linear infinite;
  border-radius: 50%;  /* make container round */
  overflow: hidden; 
}

/* Hover effect */
.skill-icon:hover {
  transform: translateY(-6px);
  box-shadow: 0 8px 20px rgba(249, 115, 22, 0.4);
  border-color: #f97316;
}

.skill-icon:hover img,
.skill-icon:hover svg {
  transform: scale(1.1);
  filter: drop-shadow(0 0 6px #f97316);
}


/* Full screen with dark neon gradient */
.loading-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: radial-gradient(circle at center, #1a1a2e, #0f0f1f);
  overflow: hidden;
  font-family: 'Arial', sans-serif;
}

/* Inner content */
.loading-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1;
}

/* Logo and text */
.loading-logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 40px;
}

/* Rotating logo image */
/* Rotating logo container */
.rotating-icon {
  width: 80px;  /* adjust size as needed */
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: rotate 3s linear infinite;
  border-radius: 50%;  /* make container round */
  overflow: hidden;    /* ensure image stays inside circle */
  box-shadow: 0 0 20px #6ec1ff, 0 0 40px #ff6ec7; /* optional neon glow */
}

/* Logo image */
.logo-img {
  width: 100%;
  height: 100%;
  object-fit: cover; /* cover ensures full circle without distortion */
  border-radius: 50%; /* make the image itself round */
  filter: drop-shadow(0 0 10px #6ec1ff) drop-shadow(0 0 20px #6ec1ff);
}


/* Neon glowing text */
.loading-text {
  font-size: 2rem;
  font-weight: bold;
  color: #ff6ec7;
  text-align: center;
  text-shadow:
    0 0 5px #ff6ec7,
    0 0 10px #ff6ec7,
    0 0 20px #ff6ec7,
    0 0 40px #ff6ec7;
  animation: glow 2s ease-in-out infinite alternate;
  margin-top: 15px;
}

/* Progress bar with glow */
.loading-progress {
  width: 250px;
  height: 10px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 0 10px #fff inset;
}

.progress-bar {
  height: 100%;
  width: 60%;
  background: linear-gradient(90deg, #6ec1ff, #ff6ec7);
  border-radius: 5px;
  animation: progress 2s ease-in-out infinite;
  box-shadow: 0 0 10px #6ec1ff, 0 0 20px #ff6ec7;
}

/* Animations */
@keyframes rotate {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@keyframes glow {
  0% { text-shadow: 0 0 5px #ff6ec7, 0 0 10px #ff6ec7, 0 0 20px #ff6ec7, 0 0 40px #ff6ec7; }
  50% { text-shadow: 0 0 15px #ff6ec7, 0 0 30px #ff6ec7, 0 0 40px #ff6ec7, 0 0 60px #ff6ec7; }
  100% { text-shadow: 0 0 5px #ff6ec7, 0 0 10px #ff6ec7, 0 0 20px #ff6ec7, 0 0 40px #ff6ec7; }
}

@keyframes progress {
  0% { transform: translateX(-100%); }
  50% { transform: translateX(0); }
  100% { transform: translateX(100%); }
}


/* Contact Section Styles */
.contact-footer {
  background-color: #f97316;
  color: #fff;
  padding: 2rem;
  border-radius: 0 5rem 0 0;
  box-shadow: 0 4px 24px rgba(0,0,0,0.08);
  margin-top: 2rem;
}

.contact-footer .contact-links p,
.contact-footer .contact-links span,
.contact-footer .contact-links a {
  color: #fff;
}

.contact-footer .social-link {
  background-color: #fff;
  color: #f97316;
  transition: background 0.3s, color 0.3s;
}

.contact-footer .social-link:hover {
  background-color: #1f2937;
  color: #fff;
}

/* Toast Notification */
.toast {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  background-color: #1f2937;
  color: #ffffff;
  padding: 1rem;
  border-radius: 0.5rem;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.3);
  z-index: 1000;
  opacity: 0;
  transform: translateY(2rem);
  transition: all 0.3s;
}

.toast.show {
  opacity: 1;
  transform: translateY(0);
}

.toast-title {
  font-weight: 600;
  margin-bottom: 0.25rem;
}

.toast-description {
  font-size: 0.875rem;
  color: #d1d5db;
}

/* Services Section */
.services-section {
  margin-top: 2rem;
}

.services-grid {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-top: 1rem;
}

.service-card {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  padding: 1.5rem;
  border: 1px solid #374151;
  transition: border-color 0.3s;
}

.service-card:hover {
  border-color: #f97316;
}

.service-icon {
  color: #f97316;
  flex-shrink: 0;
}

.service-icon svg {
  width: 2rem;
  height: 2rem;
}

.service-title {
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

/* Contact Links */
.contact-links {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-bottom: 1rem;
  margin-top: 2rem;
}

.contact-links p {
  margin: 0;
  color: #d1d5db;
}

/* Contact Form Section */
.contact-form-section {
  margin-bottom: 2rem;
}

.contact-info-section {
  margin-top: 2rem;
}

@media (min-width: 768px) {
  .contact-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 4rem;
  }
  
  .services-grid {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .contact-form-section {
    margin-bottom: 0;
  }
  
  .contact-info-section {
    margin-top: 0;
  }
}
