/* Hero Section */
.hero {
    min-height: 100vh;
    display: flex;
    align-items: center;
    padding: 5rem 1rem 0;
    position: relative;
}

.hero-container {
    max-width: 1280px;
    margin: 0 auto;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr;
    gap: 3rem;
    align-items: center;
}

.hero-content {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.hero-title {
    font-size: 3rem;
    font-weight: bold;
    line-height: 1;
}

.hero-subtitle {
    font-size: 1.25rem;
    color: #d1d5db;
}

.hero-name {
    color: #ffffff;
    font-weight: 600;
}

.hero-role {
    font-size: 2rem;
    font-weight: bold;
    line-height: 1;
}

.hero-buttons {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    padding-top: 1.5rem;
}
.typing-cursor {
    color: #f97316;
    animation: blink 1s infinite;
    font-weight: normal;
}

@keyframes blink {
    0%, 50% { opacity: 1; }
    51%, 100% { opacity: 0; }
}
.hero-skills {
    display: flex;
    flex-wrap: wrap;;
    padding-top: 1rem;
     font-size: 1.3rem;
    color: #f97316;
    font-weight: 500;
    min-height: 1.5rem;
    align-items: center;
    gap: 0.5rem;
    /* background-color: white; */
}
.typing-text{
    display: inline-block;
    /* width: 20%; */
    font-size: 1.3rem;
    white-space: nowrap;
    overflow: hidden;
    animation: typing 4s steps(30, end), blink-caret 0.75s step-end infinite;
}
@keyframes typing {
    from { width: 0; }
    to { width: 50%; }
}

.hero-image {
    display: block;
}

.hero-image-container {
    position: relative;
    display: flex;
    justify-content: center;
}
.hero-image-bg {
    width: 16rem;
    height: 16rem;
    border-radius: 50%;
    background: linear-gradient(135deg, #f97316, #dc2626);
    display: flex;
    align-items: center;
    justify-content: center;
}

.hero-img {
    width: 14rem;
    height: 14rem;
    border-radius: 50%;
    object-fit: cover;
    border: 4px solid #111827;
}

.hero-decoration-1 {
    position: absolute;
    top: -1rem;
    right: -1rem;
    width: 2rem;
    height: 2rem;
    top: -0.5rem;
    right: 2rem;
    border: 2px solid #f97316;
    transform: rotate(45deg);
}

.hero-decoration-2 {
    position: absolute;
    width: 1rem;
    height: 1rem;
    bottom: -0.5rem;
    left: 1rem;
    border: 2px solid #f97316;
    transform: rotate(45deg);
}

.scroll-indicator {
    position: absolute;
    bottom: 2rem;
    left: 50%;
    transform: translateX(-50%);
    animation: bounce 2s infinite;
}

.scroll-icon {
    width: 2rem;
