/* Projects Section */
.projects {
    padding: 5rem 1rem;
    background: #111827;
    color: #fff;
}

.projects-header {
    text-align: center;
    margin-bottom: 4rem;
}

.section-divider {
    width: 4rem;
    height: 4px;
    background-color: #f97316;
    margin: 1rem auto 0;
}

/* Carousel */
.carousel {
    position: relative;
    overflow: hidden;
}

.carousel-inner {
    display: flex;
    transition: transform 0.6s ease-in-out;
}

.project {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 3rem;
    align-items: center;
    background-color: #1f2937;
    border-radius: 1rem;
    padding: 2rem;
    margin-bottom: 2rem;
    transition: transform 0.3s cubic-bezier(.4,2,.3,1), box-shadow 0.3s;
    box-shadow: 0 8px 24px rgba(8, 80, 236, 0.15);
    flex: 0 0 auto;
    box-sizing: border-box;
}

.project:hover {
    transform: translateY(-10px) scale(1.03);
    box-shadow: 0 16px 40px rgba(8, 80, 236, 0.25);
}

.project-content {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.project-title {
    font-size: 1.5rem;
    font-weight: bold;
}

.project-tags {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
}

.tag {
    padding: 0.25rem 0.75rem;
    background-color: #374151;
    color: #d1d5db;
    font-size: 0.875rem;
    border-radius: 0.25rem;
}

.project-description {
    color: #d1d5db;
    line-height: 1.75;
}

.project-buttons {
    display: flex;
    gap: 1rem;
}

.project-image img {
    border-radius: 0.5rem;
    width: 100%;
    height: 16rem;
    object-fit: cover;
}

/* Carousel buttons */
.carousel-btn {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: rgba(17, 24, 39, 0.8);
    border: none;
    padding: 12px;
    border-radius: 50%;
    cursor: pointer;
    color: #f97316;
    font-size: 1.2rem;
    z-index: 10;
}

.carousel-btn.prev {
    left: 10px;
}

.carousel-btn.next {
    right: 10px;
}

.carousel-btn:hover {
    background: #f97316;
    color: #111827;
}

/* Indicators */
.carousel-indicators {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    gap: 8px;
}

.indicator {
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background: #111827;
    border: 1px solid #f97316;
    cursor: pointer;
    transition: all 0.3s ease;
}

.indicator.active {
    background: #f97316;
}

/* Responsive */
@media (max-width: 768px) {
    .project {
        grid-template-columns: 1fr;
    }
    .project-image img {
        height: 12rem;
    }
}

