}
.contact-info {
    background-color: #f97316;
    color: #fff;
    padding: 2rem;
    border-radius: 0 5rem 0 0;
    box-shadow: 0 4px 24px rgba(0,0,0,0.08);
    margin-top: 2rem;
}

.contact-info .contact-links p,
.contact-info .contact-links span,
.contact-info .contact-links a {
    color: #fff;
}

.contact-info .social-link {
    background-color: #fff;
    color: #f97316;
    transition: background 0.3s, color 0.3s;
}

.contact-info .social-link:hover {
    background-color: #1f2937;
    color: #fff;
}
.footer {
    /* background: #1f2937; */
    color: #fff;
    text-align: center;
    /* padding: 1.5rem 0 1rem 0; */
    font-size: 1rem;
    letter-spacing: 0.5px;
    /* border-radius: 0 0 1rem 1rem; */
    /* margin-top: 2rem; */
    /* box-shadow: 0 -2px 16px rgba(17, 24, 39, 0.08); */
}

.footer p {
    margin: 0;
    color: #fff;
    font-size: 1rem;
}

.footer span,
.footer .copyright-emoji {
    color: #f97316;
    font-weight: bold;
    margin-right: 0.25rem;
}
/* Toast */
.toast {
    position: fixed;
    bottom: 2rem;
    right: 2rem;
    background-color: #1f2937;
    color: #ffffff;
    padding: 1rem;
    border-radius: 0.5rem;
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.3);
    z-index: 1000;
    opacity: 0;
    transform: translateY(2rem);
    transition: all 0.3s;
}

.toast.show {
    opacity: 1;
    transform: translateY(0);
}

.toast-title {
    font-weight: 600;
    margin-bottom: 0.25rem;
}

.toast-description {
    font-size: 0.875rem;
    color: #d1d5db;
}

/* Animations */
@keyframes bounce {
    0%, 20%, 53%, 80%, 100% {
        transform: translate3d(-50%, 0, 0);
    }
    40%, 43% {
        transform: translate3d(-50%, -10px, 0);
    }
    70% {
        transform: translate3d(-50%, -5px, 0);
    }
    90% {
        transform: translate3d(-50%, -2px, 0);
    }
}

/* Responsive Design */
@media (min-width: 768px) {
    .nav-desktop {
        display: flex;
    }
    
    .nav-mobile-btn {
        display: none;
    }
    
    .hero-title {
        font-size: 4rem;
    }
    
    .hero-subtitle {
        font-size: 1.5rem;
    }
    
    .hero-role {
        font-size: 3rem;
    }
    .hero-image {
        display: block;
    }
    .services-grid {
        grid-template-columns: repeat(3, 1fr);
    }
    
    .contact-grid {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (min-width: 1024px) {
    .boy{
    width: 5rem;
    height: 5rem;
    background-color: #f97316;
    border-radius: 50%;
    display: inline-block;
}
    .hero-container {
        grid-template-columns: 1fr 1fr;
    }
    
    .hero-title {
        font-size: 5rem;
    }
    
    .hero-role {
        font-size: 4rem;
    }
    
    .hero-image {
        display: block;
        order: 0;
        margin-bottom: 0;
    }
    
    .hero-image-bg {
        width: 20rem;
        height: 20rem;
    }
    
    .hero-img {
        width: 18rem;
        height: 18rem;
    }
    
    .hero-decoration-1 {
        top: -1rem;
        right: -1rem;
        width: 2rem;
        height: 2rem;
    }
    
    .hero-decoration-2 {
        bottom: -1rem;
        left: -1rem;
        width: 1.5rem;
        height: 1.5rem;
    }
    .about-grid {
        grid-template-columns: 1fr 1fr;
    }
    
    .about-image {
        display: block;
    }
    
    .project {
        grid-template-columns: 1fr 1fr;
    }
    
    .project-reverse {
        direction: rtl;
    }
    
    .project-reverse > * {
        direction: ltr;
    }
}
/* Fix Floating Icons - Make sure they stay in Projects section only */
.projects {
  position: relative;
  overflow: hidden;
}

.floating-icons {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1; /* Lower z-index so they stay behind content */
}

.floating-icon {
  position: absolute;
  width: 40px;
  height: 40px;
  color: #f97316;
  opacity: 0.1; /* Make them more subtle */
  animation: float 6s ease-in-out infinite;
}

/* Make sure other sections don't have floating icons */
.about .floating-icons,
.testimonials .floating-icons,
.contact .floating-icons {
  display: none;
}

